.medieval-letter-container {
  margin: 20px auto;
  max-width: 500px;
  width: 100%;
  aspect-ratio: 1.4 / 1;
  position: relative;
  perspective: 1500px;
  transition: all 0.8s ease;
  z-index: 10;
}

/* Base of the envelope body */
.envelope-body-base {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

/* The actual letter content inside the envelope */
.parchment-content {
  position: absolute;
  top: 10%;
  left: 5%;
  width: 90%;
  height: 80%;
  padding: 30px;
  box-sizing: border-box;
  background: #fffaf0; /* Floral white parchment */
  border-radius: 8px;
  box-shadow: inset 0 0 50px rgba(0,0,0,0.05);
  z-index: 2;
  opacity: 0;
  transform: translateY(20px) scale(0.9);
  transition: all 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
  pointer-events: none;
  overflow-y: auto;
  color: #4a0e0e;
}

.parchment-content p {
  white-space: pre-wrap;
  font-family: 'Dancing Script', cursive;
  font-size: 1.4rem;
  line-height: 1.6;
  text-align: left;
}

/* Flaps */
.envelope-left-flap,
.envelope-right-flap,
.envelope-bottom-flap {
  position: absolute;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  z-index: 3;
  transition: all 0.6s ease;
}

.envelope-left-flap {
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  clip-path: polygon(0 0, 100% 50%, 0 100%);
  border-radius: 12px 0 0 12px;
}

.envelope-right-flap {
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  clip-path: polygon(100% 0, 0 50%, 100% 100%);
  border-radius: 0 12px 12px 0;
}

.envelope-bottom-flap {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60%;
  clip-path: polygon(0 100%, 50% 0, 100% 100%);
  border-radius: 0 0 12px 12px;
}

/* Top Flap */
.envelope-top-flap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60%;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  clip-path: polygon(0 0, 50% 100%, 100% 0);
  transform-origin: top center;
  z-index: 4;
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 20px;
  border-radius: 12px 12px 0 0;
}

/* Open state */
.medieval-letter-container.open .envelope-top-flap {
  transform: rotateX(180deg);
  z-index: 0;
}

.medieval-letter-container.open .parchment-content {
  opacity: 1;
  transform: translateY(-40px) scale(1.05); /* Slide up a bit when open */
  pointer-events: auto;
  z-index: 10;
  height: 120%; /* Expand to show more content */
  box-shadow: 0 20px 40px rgba(0,0,0,0.2);
}

.medieval-letter-container.open .envelope-left-flap,
.medieval-letter-container.open .envelope-right-flap,
.medieval-letter-container.open .envelope-bottom-flap {
  opacity: 0.5;
  transform: scale(0.95);
}

/* Responsive */
@media (max-width: 480px) {
  .medieval-letter-container {
    aspect-ratio: 1.2 / 1;
  }
  .parchment-content p {
    font-size: 1.1rem;
    padding: 10px;
  }
  .medieval-letter-container.open .parchment-content {
    transform: translateY(-20px) scale(1.02);
    height: 110%;
    width: 95%;
    left: 2.5%;
  }
}
