.letter-stamp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  z-index: 20;
}

.wax-seal {
  width: 80px;
  height: 80px;
  background-color: #d00000; /* Richer red for wax */
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    inset 0 0 10px rgba(0, 0, 0, 0.4),
    0 4px 10px rgba(0, 0, 0, 0.3);
  border: 2px solid #a00000;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
}

.wax-seal::after {
  content: '';
  position: absolute;
  width: 90%;
  height: 90%;
  border-radius: 50%;
  border: 1px dashed rgba(255, 255, 255, 0.2);
}

.wax-seal:hover {
  transform: scale(1.1) rotate(5deg);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4);
}

.seal-letter {
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
  color: #ffd700; /* Gold letter */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  user-select: none;
}

.stamp-instruction {
  margin-top: 12px;
  font-family: 'Poppins', sans-serif;
  font-size: 0.9rem;
  color: #4a0e0e;
  font-weight: 600;
  background: rgba(255, 255, 255, 0.5);
  padding: 4px 12px;
  border-radius: 20px;
  backdrop-filter: blur(4px);
}

@media (max-width: 480px) {
  .wax-seal {
    width: 60px;
    height: 60px;
  }
  .seal-letter {
    font-size: 2rem;
  }
}
