.image-gallery-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 95%;
  max-width: 600px;
  max-height: 85vh;
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 30px;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  padding: 40px 20px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-gallery-container.pulled {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
}

.gallery-close-btn {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 2rem;
  color: #ff4d6d;
  cursor: pointer;
  z-index: 1001;
  transition: transform 0.2s ease;
}

.gallery-close-btn:hover {
  transform: scale(1.2) rotate(90deg);
}

.slider-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 15px;
  position: relative;
}

.slider-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 0.5s ease-in-out;
}

.image-container {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  background: #eee;
}

.slider-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.image-caption {
  margin-top: 20px;
  font-family: 'Dancing Script', cursive;
  font-size: 1.8rem;
  color: #ff4d6d;
  text-align: center;
  min-height: 3rem;
  padding: 0 10px;
}

.slider-nav {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.2rem;
  color: #ff4d6d;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.slider-nav:hover {
  background: #ff4d6d;
  color: white;
  transform: scale(1.1);
}

.slider-dots {
  display: flex;
  gap: 8px;
  margin-top: 15px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: #ff4d6d;
  width: 25px;
  border-radius: 10px;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

@media (max-width: 480px) {
  .image-gallery-container {
    padding: 30px 10px;
  }
  .image-caption {
    font-size: 1.4rem;
  }
  .slider-nav {
    width: 35px;
    height: 35px;
  }
}
